import sass from './images/777.png'
import html from './images/OIP (1).png'
import js from './images/OIP (2).png'
import react from './images/react-logo_418x418.png'
import bootstrap from './images/OIP (4).png'
import css from './images/OIP.jpg'
import firebase from './images/R (1).png'
import reduxToolkit from './images/R.png'


let arrayimage = [sass , html , js , react , bootstrap , css , firebase , reduxToolkit] 
let arrayinfo = ['Sass' , 'Html' , 'Js' , 'React' , 'Bootstrap' , 'Css' , 'Firebase' , 'Redux-Toolkit'] 

function Skills(){
    return(
        <>
            <div className='skills' id='skills'>
                <div className='skills_header'>
                    <h1><span>MY</span> SKILLS</h1>
                </div>
                <div className='skills_content'>

                    {arrayimage.map((inde , index)=>{
                        return(
                            <div>
                            <img src={arrayimage[index]} alt=''/>
                            <p>{arrayinfo[index]}</p>
                        </div>
                        )
                    })}
                </div>
            </div>
        </>
    )
}

export default Skills;