import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

function NAVBAR(){
    return(
        <>
<div className="navbar">
    <Navbar  expand="lg">
        <Container>
            <Navbar.Brand className='col-5 brand' href="#home">PORTFOLIO</Navbar.Brand>

            <Navbar.Toggle className='toggle' aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="me-auto">
                    <Nav.Link href="#home" className='nav_link'><i class="fa-solid fa-house"></i><a class="menu__link2" href="#home">Home</a></Nav.Link>
                    <Nav.Link href="#about" className='nav_link'><i class="fa-solid fa-address-card"></i><a class="menu__link2" href="#about">About</a></Nav.Link>
                    <Nav.Link href="#skills" className='nav_link'><i class="fa-brands fa-react"></i><a class="menu__link2" href="#skills">Skills</a></Nav.Link>
                    <Nav.Link href="#portfolio" className='nav_link'><i class="fa-solid fa-briefcase"></i><a class="menu__link2" href="#portfolio">Portfolio</a></Nav.Link>
                    <Nav.Link href="#cantact" className='nav_link'><i class="fa-solid fa-address-book"></i><a class="menu__link2" href="#cantact">Contact</a></Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Container>
    </Navbar>
            </div>
        </>
    )
}


export default NAVBAR;