import chess from  '../images/chess.png'
import restaurant from '../images/restaurant.png'
import restaurant2 from '../images/restaurant2.png'
import flower from '../images/flower.png'
import mtn from '../images/mtn.png'
import portfolio from '../images/portfolio.png'
import trix from '../images/trix.png'
import managerrest from '../images/managerrest.png'


function Portfolio(){




    let arrayimage = [chess , restaurant , restaurant2 , flower , mtn , portfolio , trix , managerrest]
    let arrayinfo =['Chess' , 'Restaurant' , 'Restaurant2' , 'Flower' , 'Mtn' , 'Portfolio' , 'Trix' , 'Manager Rest2']
    let arraylink =['http://chess-bestt.surge.sh/' , 'http://restaurant-best.surge.sh/' , 'http://restaurant2-best.surge.sh/' , 'http://flower-best.surge.sh/' , 'http://mtn-best.surge.sh/' , 'http://portfolio-best.surge.sh/' , 'http://trix-best.surge.sh/' , 'http://managerrest2-best.surge.sh/']
    let arraylink_facebook = ['https://fb.watch/kre6Q-jAlb/' , 'https://fb.watch/krgXw7yYsj/' , 'https://fb.watch/krhxoOaEeO/' , 'https://fb.watch/krf2O_AGBi/ ' , 'https://fb.watch/kregrvwDUO/' , 'https://fb.watch/krJ1Eyxkf1/' , 'https://fb.watch/kre7_79e9S/' , 'https://fb.watch/kriLTUMxp0/'];
    return(
        <div className="portfolio" id='portfolio'>
            <div className="portfolio_header">
                <h1>MY <span>WORK</span></h1>
            </div>
            <div className="portfolio_content">

            {arrayimage.map((g,index)=>{
                return(
                    <>
                        <div>
                            <div>
                                <h3>{arrayinfo[index]}</h3>
                                <a target='_blanck' href={arraylink[index]}>Click to view the site</a>
                                <a target='_blanck' href={arraylink_facebook[index]}>Click to watch the explanation video</a>
                            </div>
                            <div>
                                <img src={arrayimage[index]} alt=''/>
                            </div>
                        </div>
                    </>
                )
            })}
                


            </div>
        </div>
    )
}
export default Portfolio;